<template>
  <div>
    <v-topTab selectPageTitle="巡检模板管理"></v-topTab>
    <div class="wrapper-md" style="background-color: #ccccccbf;">
      <div class="panel panel-default">
        <div class="row wrapper">
          <label class="fl" style="line-height:3rem">时间：</label>
          <div class="fl m-b-xs" style="line-height:2.8rem">
            <input type="date" v-model="queryData.params.startTime" /> -
            <input type="date" v-model="queryData.params.endTime" />
          </div>
            <button type="buttton" @click="searchList" class="btn btn_style fl">查询</button>
            <button type="buttton" @click="resetSearch" class="btn btn_style fl">重置</button>
			<a href="http://r3x0867731.imdo.co/inspectionRecord/export" class="btn btn_style fr">导出</a>
            <button type="buttton" @click="addOpt" class="btn btn_style fr">新增</button>
        </div>
        <div class="table-responsive">
          <table class="table table-striped b-t b-light">
            <thead>
              <tr>
                <th></th>
                <th style="width:20px;">
                  <input
                    class="form-check-input"
                    type="checkbox"
                    required
                    v-model="checkAll"
                    :checked="checkAll"
                    @click="setCheckAll($event);"
                  />
                </th>
                <th>#</th>
                <th>巡检设备</th>
                <th>巡检部位</th>
                <th>巡检结论</th>
                <th>巡检人(钳工)</th>
                <th>巡检人(电工)</th>
                <th>班次</th>
                <th>检查人</th>
                <th>描述</th>
                <th>时间</th>
                <th>操作</th>
              </tr>
            </thead>
            <tbody  v-if="inspectionRecordList.length>0">
              <tr v-for="(item,index) in inspectionRecordList" :key="index">
                <td>+</td>
                <td>
                  <input
                    class="form-check-input"
                    type="checkbox"
                    required
                    @click="setCheckOpt($event,item.id);"
                  />
                </td>
                <td>{{index+1}}</td>
                <td>{{item.serial}}</td>
                <td>{{item.templateId}}</td>
                <td>{{item.conclusion}}</td>
                <td>{{item.staff1}})</td>
                <td>{{item.staff2}}</td>
                <td>{{item.duty}}</td>
                <td>{{item.checkPerson}}</td>
                <td>{{item.description}}</td>
                <td>{{timeFormat(item.createTime)-timeFormat(item.updateTime)}}</td>
                <td>
                  <a class="fl width50 opt" @click="updateByUserId(index)">修改</a>
                  <a class="fl width50 opt" @click="deleteByIdInfo(item.id)">删除</a>
                </td>
              </tr>
            </tbody>
          </table>
            <div v-if="inspectionRecordList.length==0" class="cont">暂无数据</div>
        </div>
      </div>
    </div>
        <footer class="panel-footer width" v-if="inspectionRecordList.length>0" style="right: 0;bottom: 0;">
          <div class="row">
            <div class="text-right text-center-xs fr" style="width: 80%;
    padding: 0px 3%;">
              {{(listData.page-1)*20+1}}-{{listData.page*20}} 共 {{listData.totalNum}}条
              <span class="marginLeft1 opt" style="color:#2793e0" @click="page_click($event,1)">首页</span>
              <span class="marginLeft1 opt" :style="listData.page==1?'color:#ccc':'color:#2793e0'" @click="page_click($event,listData.page-1,listData.page==1)">上一页</span>
              <span class="marginLeft1">第{{listData.page}}页</span>
              <span class="marginLeft1">共{{listData.totalPage}}页</span>
              <span class="marginLeft1 opt" :style="listData.page == listData.totalPage?'color:#ccc':'color:#2793e0'" @click="page_click($event,listData.page+1,listData.page == listData.totalPage)">下一页</span>
              <span class="marginLeft1 opt" style="color:#2793e0" @click="page_click($event,listData.totalPage)">尾页</span>
              <span class="marginLeft1">跳至第</span> <input type="number" style="width:5rem;margin: 0 1%;" @blur="page_click($event)"> 页
            </div>
          </div>
        </footer>
    <div id="test"></div>
    <div id="log_window">
         <div class="titleInfo width backgroundFFF">
             <h3 class=" lineHeight5 cont" style="margin:0">
                 <span v-if="obj==null||obj.id==undefined">添加</span>
                 <span v-if="obj!=null&&obj.id!=undefined">修改</span>
                 巡检模板信息</h3>
             <span class="fr closeX" @click="closeDlg">x</span>
        </div>
       <div class="titleInfo width marginTop1" style="height: 40rem;
    background-color: #fff;overflow-y:scroll">
            <div class="item_info fl">
               <label class="lable_Style">巡检设备(*):</label>
               <input class="width63" v-model="obj.serial"/>
           </div>
           <div class="item_info fl">
               <label class="lable_Style">巡检部位(*):</label>
               <input class="width63" v-model="obj.templateId"/>
           </div>
           <div class="item_info fl">
               <label class="lable_Style">巡检结论(*):</label>
               <input class="width63" v-model="obj.conclusion"/>
           </div>
           <div class="item_info fl">
               <label class="lable_Style">巡检人(钳工)(*):</label>
               <input class="width63" v-model="obj.staff1"/>
           </div>
           <div class="item_info fl">
               <label class="lable_Style">巡检人(电工)(*):</label>
               <input class="width63" v-model="obj.staff2"/>
           </div>
           <div class="item_info fl">
               <label class="lable_Style">检查人(*):</label>
               <input class="width63" v-model="obj.checkPerson"/>
           </div>
           <div class="item_info fl">
               <label class="lable_Style">班次(*):</label>
               <input class="width63" v-model="obj.duty"/>
           </div>
           <div class="item_info fl">
               <label class="lable_Style">上班遗留问题（机械）：</label>
               <input class="width63" v-model="obj.problem1"/>
           </div>
           <div class="item_info fl">
               <label class="lable_Style">上班遗留问题（电气）：</label>
               <input class="width63" v-model="obj.problem2"/>
           </div>
           <div class="item_info fl">
               <label class="lable_Style">本班发现问题（机械）：</label>
               <input class="width63" v-model="obj.problem3"/>
           </div>
           <div class="item_info fl">
               <label class="lable_Style">本班发现问题（电气）：</label>
               <input class="width63" v-model="obj.problem4"/>
           </div>
           <div class="item_info fl">
               <label class="lable_Style">本班遗留问题（机械）：</label>
               <input class="width63" v-model="obj.problem5"/>
           </div>
           <div class="item_info fl">
               <label class="lable_Style">本班遗留问题（电气）：</label>
               <input class="width63" v-model="obj.problem6"/>
           </div>
           <div class="width fl" style="color:red;padding:0 2%;">注：*为必填项</div>
        <div class="width backgroundFFF fl" style="padding: 2% 0;height:5rem">
            <button class="btn_style fr" style="margin-right: 3%;" @click="updateInfoPost">
                <span v-if="obj==null||obj.id==undefined">添加</span>
                <span v-if="obj!=null&&obj.id!=undefined">修改</span>
                </button>
            <button class="btn_style fr" style="margin-right: 3%;" @click="closeDlg">取消</button>
        </div>
        </div>
    </div>
  </div>
</template>
<script>
import topTab from "../block/TopTab";
import { attendList, attendAdd } from "../../assets/api/axiosHttp";
export default {
  name: "inspectionManager",
  data() {
    return {
      listData: {
          page:1,
          totalNum:0,
          totalPage:1
      },
      notifications:3,
      inspectionRecordList:[],
      checkAll: false,
      exportData:{
          startTime:"",
          endTime:"",
          idArrs:[]
      },
      obj: {},
      queryData: {
        params: {
          endTime:"2020-04-14",
          page: 1,
          rows: 20,
          startTime: "2020-04-18"
        }
      }
    };
  },
  mounted(){
    this.queryData.params.startTime = this.getDate(10);
    this.queryData.params.endTime = this.getDate(-10);
  },
  created() {
    this.init();
  },
  methods: {
    init() {
      this.$ajax
        .get("/inspectionRecord/list", this.queryData, {
          headers: { "Access-Control-Allow-Origin": "*" }
        })
        .then(response => {
            this.listData = response.data.data;
            this.inspectionRecordList = response.data.data.data;
        })
        .catch(response => {
          console.log(response);
        });
    },
    getDate(n){
       var n = n;
        var d = new Date();
        var year = d.getFullYear();
        var mon = d.getMonth() + 1;
        var day = d.getDate();
        if(day <= n) {
            if(mon > 1) {
                mon = mon - 1;
            } else {
                year = year - 1;
                mon = 12;
            }
        }
        d.setDate(d.getDate() - n);
        year = d.getFullYear();
        mon = d.getMonth() + 1;
        day = d.getDate();
        let s = year + "-" + (mon < 10 ? ('0' + mon) : mon) + "-" + (day < 10 ? ('0' + day) : day);
        return s;
    },
    page_click(event,page,flag){
        let type = event.type;
        if(type == 'blur'){
            this.queryData.params.page = event.target.value;
            this.init();
        }else{
            if(flag==undefined){
                this.queryData.params.page = page;
                this.init();
            }else{
                if(!flag){
                    this.queryData.params.page = page;
                    this.init();
                }
            }
        }

    },
    searchList() {
        this.init();
    },
    resetSearch() {
        this.queryData.params.startTime = this.getDate(10);
        this.queryData.params.endTime = this.getDate(-10);
        this.init();
      },
    addOpt(){
        console.info("=======addInfo=======");
       var s = document.getElementById("test");
        s.style.display = "block";
    
        var l = document.getElementById("log_window");
        l.style.display = "block";
    },
    exportOpt() {
        console.info("=======exportOpt=======");
       this.$ajax
        .get("/inspectionRecord/export",this.exportData,{
          headers: { "Access-Control-Allow-Origin": "*" }
        })
        .then(response => {
          if(response.data.code==200){
            alert('导出文件成功')
            this.init();
          }
        })
        .catch(response => {
          console.log(response);
        });
    },
    deleteByIdInfo(id) {
      console.info("=======deleteByIdInfo=======" + id);
       this.$ajax
        .delete("/inspectionRecord/delete/"+id,{
          headers: { "Access-Control-Allow-Origin": "*" }
        })
        .then(response => {
          if(response.data.code==200){
            alert('删除信息成功')
            this.init();
          }
        })
        .catch(response => {
          console.log(response);
        });
    },
    closeDlg(){
        var s = document.getElementById("test");
        s.style.display = "none";
    
        var l = document.getElementById("log_window");
        l.style.display = "none";
    },
    updateByUserId(index) {
      this.obj = this.inspectionRecordList[index];
      // this.obj.createTime = this.timeFormat(this.obj.createTime);
      // this.obj.entryTime = this.timeFormat(this.obj.entryTime);
      // this.obj.inspectionTime = this.timeFormat(this.obj.inspectionTime);
      // this.obj.outTime = this.timeFormat(this.obj.outTime);
      // this.obj.updateTime = this.timeFormat(this.obj.updateTime);
      console.info("=======updateByUserId=======" + index);
       var s = document.getElementById("test");
        s.style.display = "block";
    
        var l = document.getElementById("log_window");
        l.style.display = "block";

    },
    updateInfoPost(){
         if(this.formDataFormat(this.obj)){
         if(this.obj.id == undefined){
            this.$ajax.post("/inspectionRecord/add", {attendance:JSON.stringify(this.obj)}, {
            headers: { "Access-Control-Allow-Origin": "*" }
            }).then(response => {
                if(response.data.code==200){
                    alert('添加信息成功')
                this.closeDlg();
                this.init();
            }
            }).catch(response => {
            console.log(response);
            });
         }else{
            this.$ajax.post("/inspectionRecord/modify", {attendance:JSON.stringify(this.obj)}, {
            headers: { "Access-Control-Allow-Origin": "*" }
            }).then(response => {
                if(response.data.code==200){
                    alert('修改信息成功')
                this.closeDlg();
                this.init();
            }
            }).catch(response => {
            console.log(response);
            });
         }
        
        var s = document.getElementById("test");
        s.style.display = "none";
        var l = document.getElementById("log_window");
        l.style.display = "none";
         }
    },
    formDataFormat(inspecteData){
      //{serial、templateId、conclusion、staff1、staff2、checkPerson、duty}
      if(inspecteData.serial!='' && inspecteData.serial!=undefined && 
         inspecteData.templateId!='' && inspecteData.templateId!=undefined && 
         inspecteData.conclusion!='' && inspecteData.conclusion!=undefined && 
         inspecteData.staff1!='' && inspecteData.staff1!=undefined && 
         inspecteData.staff2!='' && inspecteData.staff2!=undefined && 
         inspecteData.checkPerson!='' && inspecteData.checkPerson!=undefined && 
         inspecteData.duty!='' &&  inspecteData.duty!=undefined
        ){
          return true;
        }else{
          return false;
        }
    },
    timeFormat(value) {
      let date = new Date(value);
      let Y = date.getFullYear() + "-";

      let M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";

      let D = date.getDate() + " ";

      let h = date.getHours() + ":";

      let m = date.getMinutes() + ":";

      let s = date.getSeconds();

      return Y + M + D + h + m + s;
    },
    setCheckAll(event){
        let checked = event.target.checked;
        let arrs = document.getElementsByClassName('form-check-input');
        if(checked){
            for(let i=0;i<arrs.length;i++){
                arrs[i].checked = true;
            }
            for(let a=0;a<this.inspectionRecordList.length;a++){
                this.exportData.idArrs.push(this.inspectionRecordList[a].id);
            }
        }else{
            for(let i=0;i<arrs.length;i++){
                arrs[i].checked = false;
            }
            this.exportData.idArrs = [];
        }
        console.info("exportData.idArrs："+this.exportData.idArrs)
    },
    setCheckOpt(event,id){
        let checked = event.target.checked;
        let idArrs = this.exportData.idArrs;
        if(checked){
            let flag = false;
            for(let a=0;a<idArrs.length;a++){
                if(idArrs[a] == id){
                    flag = true;
                }
            }
            if(!flag){
                this.exportData.idArrs.push(id);
            }
        }else{
            for(let a=0;a<idArrs.length;a++){
                if(idArrs[a] == id){
                    this.exportData.idArrs.splice(a,1);
                }
            }
        }
        console.info("exportData.idArrs："+this.exportData.idArrs)
    }
  },
  components: {
    "v-topTab": topTab
  },
};
</script>
<style scoped>
html,
body {
  margin: 0;
  height: 100%;
}
#test {
  width: 100%;
  height: 100%;
  background-color: #000;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  opacity: 0.3;
  /*兼容IE8及以下版本浏览器*/
  filter: alpha(opacity=30);
  display: none;
}

#log_window {
  width: 60%;
  margin: 0 20%;
  height: 39rem;
  background-color: #cccccc;
  position: absolute;
  margin-top: 5rem;
  z-index: 3;
  top: 3%;
  bottom: 0;
  left: 0;
  right: 0;
  display: none;
}
</style>


