<template>
  <div
    class="app"
    :class="{'app-aside-folded':!sidebar,'app-header-fixed':settings.headerFixed,'app-aside-fixed':settings.asideFixed,'app-aside-folded':settings.asideFolded,'app-aside-dock':settings.asideDock,container:settings.container}"
  >
    <div class="hederDiv width">
      <v-header></v-header>
    </div>
    <div class="contentDiv">
      <div class="leftNav">
        <v-nav></v-nav>
      </div>
      <div class="rightContent">
        <div class="fl width12">
          <v-setings></v-setings>
        </div>
        <div class="fr width88">
          <div class="app-content">
            <div class="app-content-body">
              <transition
                mode="out-in"
                enter-active-class="animated fadeInUp"
                leave-active-class="animated fadeInDown"
              >
                <router-view></router-view>
              </transition>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import header from "./components/block/Header";
import nav from "./components/block/Nav";
import setings from "./components/block/Setings";
import { mapGetters, mapActions } from "vuex";
import Vue from 'vue';
export default {
  name: "app",
  components: {
    "v-header": header,
    "v-nav": nav,
    "v-setings": setings
  },
  notifications:0,
  computed: mapGetters(["sidebar", "settings"])
}
</script>
<style>
</style>



